* {
    border: 1px solid red;
}

.home-page{
    position: fixed; 
    top: 0; 
    left: 0; 
    
    /* Preserve aspet ratio */
    min-width: 100%;
    min-height: 100%;

    background-image: url('.\\images\\farm.jfif') ; 
    background-size: cover;
    background-position: center;

    display: flex;
    flex-direction: column;

    justify-content: flex-end;
    align-content: flex-end;
}

.border {
    position: fixed; 
    top: 0; 
    left: 0; 
    display: flex;
    flex-direction: column;
    margin: 5% 5% 0 5%;
    background: linear-gradient(180deg, #999999 0%, #DFDFDF 6.25%, #959392 58.85%, #C5C5C5 75%, #A6A6A6 100%);
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
    border-radius: 5px 5px 0 0;
    min-height: 100%;
    height: 100%;
    width: 90%;
}

.main-area {
    display: flex;
    flex-direction: column;
    position: relative; 
    background-color: white; 
    margin: 20px 20px 0 20px;
    height: 90%;
    
    border-radius: 15px;

}

.menubar-button {
    
    min-height: 10%;
    height: 10%;
}